var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-main',[_c('section',{staticClass:"d-md-flex d-none"},[_c('div',{staticClass:"py-8"}),_c('v-card',{staticClass:"mx-auto",staticStyle:{"background-color":"rgba(0, 0, 0, 0.7)"},attrs:{"width":"85%","flat":""}},[_c('div',{staticClass:"d-flex justify-center py-6"},[_c('div',{staticClass:"text-center"},[_c('p',{staticClass:"white--text text-h5 mb-1 pl-1",staticStyle:{"letter-spacing":"8px !important"}},[_vm._v(" 公開聲明書 ")]),_c('p',{staticClass:"text-subtitle-1 blue-grey--text text--lighten-3 mb-0"},[_vm._v(" Public statement ")])])]),_c('v-card',{ref:"statement",staticClass:"statement-card mx-auto white--text",staticStyle:{"background-color":"transparent"},attrs:{"height":"350","width":"90%","flat":""}},[_vm._v(" 歡迎您加入金峯國際會員(以下簡稱金峯國際)所提供之各項會員服務。 "),_c('br'),_vm._v(" 為保障您的權益，請詳細閱讀本服務同意書所有內容，您同意當您在線選「我同意」鍵時，即視為您已經詳細閱、了解並願意遵守本服務同意書。 "),_c('br'),_vm._v(" 注意事項之所有約定。提醒您如果您未滿二十)，您須告知法定代理人(如：父母、監護人)使其詳閱、瞭解本服務同意書內容，並取得法定代理人(如：父母、監護人)之允許始得進行， 當您使用金峯國際會員得使用之服務時，即表示您對本服務同意書之同意及其他意思表示，均視為已取得法定代理人之允許： "),_c('br'),_vm._v(" 一、遵守會員規範及法律規定： "),_c('br'),_vm._v(" 您了解您註冊成為會員後，即可使用金峯國際所提供之各服務(以下稱本服務)當會員使用本服務時，即表示您除了同意遵守本服務同意書外， 亦接受金峯國際其他針對會員之相關範包括不限客服中心内容、管理規則、公告及注意事項等相關法令之定。 "),_c('br'),_vm._v(" 二、服務簡介： "),_c('br'),_vm._v(" (一)接受本服務之會員必須自行配備上網所需之各項電設備，並負擔包括不限於「網際網路連線費用」、「電話費用」等之電信相關費用或其他服務費用。若有使用其他交易或金峯國際異業合作之服務，並須遵守及負擔該合約內容或其他衍生費用。 "),_c('br'),_vm._v(" (二)當您完成相關註冊程序後，即可具有會員資格，並可開始使用本服務，包括但不限於使用會員課程、異業合作、金流儲值服務等。 "),_c('br'),_vm._v(" 您同意當您成為金峯國際會員後使用之各項會員服務，其項服務内容之所有仍屬金峯國際或異業合作廠商及其所約定之人所有。 "),_c('br'),_vm._v(" 會員僅得依本服務同意書之約定使用，不得出租、出借、移轉或讓與給其他第三人使用。 "),_c('br'),_vm._v(" (三)金峯國際有加、更、取消、停止本服務中相關系統、服務或功能之全部或一部分，您同意金峯國際無須個別通知會員為增進會員益充服務内容，金峯國際將致力於提供會新穎之服務或功能， 您同意本會員服務同意書用於現有或將來金峯國際所提供金峯國際會員帳號得使用之各項服務。 "),_c('br'),_vm._v(" (四)金流付費平台服務為金峯國際所提供之「金流付費平台」，您所購買之點數將可享受金峯國際之特定服務。 為保障您的交易安全，請使用合法之方式進行服務儲值，若您使用虛偽不正之方式進行「儲值」或使用其他相關服務，金峯國際有隨時逕行終止您本服務之會員資格及使用各項服務之權利。 "),_c('br'),_vm._v(" (五)會員課程內容，皆為金峯國際所有，嚴禁擅自拷貝、改作、編輯、轉載、偽造、公開以任何方式侵害金峯國際著作權，或與未申請本服務之第三方使用者共享使用。 "),_c('br'),_vm._v(" (六)任何運彩投注皆有風險，投注行為皆為個人行為，您將完全自負盈虧責任，並請詳閱投注說明後，再行投注。 "),_c('br'),_vm._v(" (七)金峯國際之課程，旨在教導會員們了解各項賽事之規則，以及對賽事之分析，進而能更加瞭解各項比賽之規則，以及分析比賽可能之結果，進而有效提高賽事結果預期之精準度。 "),_c('br'),_vm._v(" 三、真實登錄義務： "),_c('br'),_vm._v(" 基於金峯國際官網所提供之各項服務，您同意於註册時依註冊申請程序所提示之項目，登錄您本人正確、真實及完整之個人資料當您的個人資料有異動時 請立即連繫官方客服更新，以維護您個人資料之真實及完整。如因您登不實資料或用他人名義致侵害他人之權益或違法時，應自負完全法律責任：您並同意您所提供個人資料若有不實或個人資料異動卻未更新致與原登資料不符時，金峯國際有随時、逕行終止您的會員資格及使用各項會員服務之權利。 "),_c('br'),_vm._v(" 四、會員規範之增訂及修改： "),_c('br'),_vm._v(" 本服務同意書如有增訂或修改，將公告於本公司官方網站，本網站不對會個別通知。如您於服務同意書變更後使用或繼續使用相關服務，將視為您已經瞭解並同意該修訂條款。 "),_c('br'),_vm._v(" 五、有下列情形，金峯國際有權將會員服務停止或更改： "),_c('br'),_vm._v(" 1.會員登錄資料不實。 "),_c('br'),_vm._v(" 2.使用中文、英文及數字以外之字元及不雅文字作為本服務之名稱。 "),_c('br'),_vm._v(" 3.使用他人的名義申請本服務。 4.違反本服務條款時。 "),_c('br'),_vm._v(" 5.其他經金峯國際判定有違公、合理方式使用本服務者。 "),_c('br'),_vm._v(" 6.會員使用本服務之行為有造成金峯國際受有損失或損害之者。 就上述停止更改服務或終止會員帳戶服務所可能產生困難、不便或危害，金峯國際對任何會員或第三人均不負任何賠償或補償責任。 "),_c('br'),_vm._v(" 六、服務暫停或中斷： "),_c('br'),_vm._v(" 金峯國際於列情形之一時，得暫停或中斷本服務之全部或一部份，對於使用者不負擔任何賠償或補償責任： "),_c('br'),_vm._v(" 1.對於本服務相關系統設備進行移、更換或維護。 "),_c('br'),_vm._v(" 2.因不可歸責於金峯國際所造成之服務停止、中斷、變更或取消。 ")]),_c('div',{staticClass:"d-flex justify-center"},[_c('v-checkbox',{attrs:{"label":"同意聲明書內容","disabled":_vm.disabled,"dark":""},model:{value:(_vm.checkbox),callback:function ($$v) {_vm.checkbox=$$v},expression:"checkbox"}})],1)],1),_c('div',{staticClass:"text-center mt-6"},[_c('v-btn',{attrs:{"dark":"","color":"blue-grey darken-4","height":"35","width":"200"},on:{"click":function($event){return _vm.gotoForm()}}},[_vm._v(" 確認 ")])],1)],1),_c('section',{staticClass:"mobil-page d-flex d-md-none"},[_c('div',{staticClass:"py-6"}),_c('div',{staticClass:"d-flex justify-end mb-4"},[_c('div',{staticClass:"pr-4"},[_c('v-img',{attrs:{"src":require("../assets/statement/m-02_title_1.png"),"width":"246"}})],1),_c('div',[_c('img',{attrs:{"src":require("../assets/statement/m-02_title_2.png"),"width":"12"}})])]),_c('v-card',{staticClass:"mx-auto",s